import { join } from 'path'

/**
 * The .edgio build directory
 */
export const EDGIO_DIR = '.edgio'

/**
 * The destination directory for JS server code
 */
export const JS_DIR = join(EDGIO_DIR, 'lambda')

/**
 * The destination directory for static assets
 */
export const ASSETS_DIR = join(EDGIO_DIR, 's3')
export const PERMANENT_ASSETS_DIR = join(EDGIO_DIR, 's3-permanent')

/**
 * The directory into which source code is copied for debugging purposes
 */
export const SOURCES_DIR = join(EDGIO_DIR, 'src')

/**
 * Returns the destination path within the lambda dir for the specified backend
 * @param backend A backend name
 * @return a file path
 */
export function pathForBackend(backend: string) {
  return join('__backends__', `${backend}.js`)
}
