import get from './utils/get'
import has from './utils/has'
import merge from 'deepmerge'
import { existsSync } from 'fs'
import { join } from 'path'
import { EDGIO_ENV_VARIABLES, LAYER0_CONFIG_FILE, EDGIO_CONFIG_FILE } from './constants'
import Validator from './utils/Validator'
import nonWebpackRequire from './utils/nonWebpackRequire'

/**
 * An object containing the config settings. This class is internal, but an instance
 * can be obtained using.
 *
 * ```js
 * import config from '@edgio/core/config'
 * ```
 *
 * @internal
 */
class Config {
  private values?: any

  /**
   * Get configuration by key. Will throw an error if no value is configured
   * @param key The key to grab from the config.
   * @param fallback A value to return if a value is not configured
   */
  get(key: string, fallback?: any): any {
    if (this.has(key)) {
      return get(this.values, key)
    } else if (typeof fallback !== 'undefined') {
      return fallback
    } else {
      throw new Error(`Edgio configuration key: "${key}" is not defined`)
    }
  }

  /**
   * Returns true if a value has been configured for a specific key, otherwise false.
   * @param key The key to test.
   */
  has(key: string): boolean {
    if (!this.values) this.loadEdgioConfig()
    return has(this.values, key)
  }

  /**
   * Returns all config values
   */
  get all() {
    if (!this.values) this.loadEdgioConfig()
    return this.values
  }

  /**
   * Loads Edgio config and sets the current configuration.
   */
  public loadEdgioConfig() {
    const file = this.loadFromFile()
    const env = this.loadFromEnv()
    this.values = this.trimStrings(merge.all([{}, this.getDefaults(), file, env]))
  }

  /**
   * Trims all string values in order to avoid errors caused by unintentional newlines.
   * Caveat: Expects no edge cases, where spaces/newlines at the start/end are desirable.
   */
  private trimStrings(object: { [key: string]: any }) {
    Object.entries(object).forEach(([key, value]) => {
      if (typeof value === 'object') {
        this.trimStrings(value)
      } else if (typeof value === 'string') {
        object[key] = value.trim()
      }
    })
    return object
  }

  /**
   * Load configuration from file
   * @private
   */
  private loadFromFile() {
    const configAbsPath = [
      join(process.cwd(), LAYER0_CONFIG_FILE),
      join(process.cwd(), LAYER0_CONFIG_FILE.replace('.js', '.cjs')),
      join(process.cwd(), EDGIO_CONFIG_FILE),
      join(process.cwd(), EDGIO_CONFIG_FILE.replace('.js', '.cjs')),
    ].find(existsSync)

    if (configAbsPath) {
      try {
        const fileConfig = nonWebpackRequire(configAbsPath)
        return Validator.validateEdgioFileConfiguration(fileConfig)
      } catch (e) {
        throw new Error(`Failed to load ${configAbsPath}: ${e.message}`)
      }
    }

    return {}
  }

  /**
   * Load configuration from env var
   * @private
   */
  private loadFromEnv() {
    if (process.env[EDGIO_ENV_VARIABLES.config]) {
      try {
        return JSON.parse(<string>process.env[EDGIO_ENV_VARIABLES.config])
      } catch (e) {
        throw new Error(
          `Edgio configuration variable: ${EDGIO_ENV_VARIABLES.config}, is not parsable as json`
        )
      }
    }

    return {}
  }

  /**
   * Returns default config.
   * Mustn't be created on bundle load time as the existence of routes
   * could change during script execution - eg. chained init into deploy.
   * @private
   */
  private getDefaults() {
    return {
      production: true,
      backends: {},
      includeNodeModules: false,
      routes:
        ['routes.js', 'routes.ts', 'routes.cjs'].find(file =>
          existsSync(join(process.cwd(), file))
        ) ?? 'routes.js',
    }
  }
}

/**
 * Create a Config instance.
 */
export default new Config()
