/* istanbul ignore file */
import { BACKENDS, HTTP_HEADERS, JS_BACKEND_HOSTNAME } from '../constants'
import { existsSync } from 'fs-extra'
import { join, basename } from 'path'
import { pathForBackend } from '../deploy/paths'
import { withTimings } from '../timing'
import loadRouter from '../router/loadRouter'
import nonWebpackRequire from '../utils/nonWebpackRequire'
import reqResMapper from './reqResMapper'
import stdStreamsWrapper, { deepRequestInspectionLogger } from './stdStreamsWrapper'
import getBodyLoggingData from './getBodyLoggingData'
import LambdaInstance, { LambdaInvocation } from './lambdaInstance'
import config from '../config'

const router = loadRouter('./' + basename(config.get('routes')))

/**
 * This is the user lambda entry point
 * @param event A lambda event
 * @param instance The lambda instance
 */
export async function handler(event: any, instance: LambdaInstance) {
  // We always create a new lambda invocations, for all jobs so that we can accurately
  // identify cold starts.
  const invocation = new LambdaInvocation(instance)
  invocation.start()

  const driLogger = deepRequestInspectionLogger(instance)

  // Deployment actions: only for internal usage and is not exposed
  switch (event.action) {
    case 'getEdgeConfig':
      return router.createEdgeConfig()
    case 'getPreloadConfig':
      return withLogging(() => router.createPreloadConfig())
  }

  let req: any, res: any, responsePromise: any
  try {
    const reqResResult = reqResMapper(event, invocation)
    req = reqResResult.req
    res = reqResResult.res
    responsePromise = reqResResult.responsePromise
  } catch (e) {
    return withLogging(() => {
      console.log('Failed to parse the event to get request and response data:', e.message, e.stack)
      return {
        body: <any>(
          Buffer.from('{"error":"Failed to parse the event to get request and response data"}')
        ),
        isBase64Encoded: true,
        statusCode: 530,
        statusMessage: 'Internal Edgio Error',
        multiValueHeaders: <any>{},
      }
    })
  }

  // Handle web requests.
  return await withLogging(
    async () => {
      const headers = req.headers || {}
      driLogger.logDownstreamRequestInfo({
        method: req.method,
        path: req.url,
        host: headers['host'],
        headers: headers,
        // We keep the protocol format same as the internal Node format on protocol fields.
        protocol: `${headers['x-0-protocol'] || 'https'}:`,
        ...getBodyLoggingData(req._rawBodyBase64, req.headers),
      })

      try {
        await ensureAppStarted(3001)
      } catch (error) {
        console.error(error)
        throw error
      }
      withTimings(() => router.run(req, res))()

      return responsePromise.then((result: any) => {
        const headers = res.headers || {}
        driLogger.logDownstreamResponseInfo({
          statusCode: res.statusCode,
          statusMessage: res.statusMessage,
          headers,
          ...getBodyLoggingData(result.body, res.headers),
        })
        return result
      })
    },
    req,
    instance
  )
}

async function startApp(port: number) {
  const modulePath = join(process.cwd(), pathForBackend(BACKENDS.js))

  if (existsSync(modulePath)) {
    let prod = nonWebpackRequire(modulePath)

    if (prod.default) {
      prod = prod.default
    }

    // backup original working directory
    const workingDir = process.cwd()

    // load the prod entry point from the framework integration package
    await prod(port)

    // set back original working directory as customers can change working path in their custom prod.js file
    process.chdir(workingDir)

    // let the router know where to proxy SSR requests
    router.setBackend(BACKENDS.js, {
      domainOrIp: JS_BACKEND_HOSTNAME,
      port,
    })

    // use default JS backend port when no port is provided
    const imageOptimizerPort = process.env.EDGIO_IMAGE_OPTIMIZER_PORT

    // check if provided env variable port can be parsed
    if (imageOptimizerPort && !/^\d+$/.test(imageOptimizerPort)) {
      console.error('Error: EDGIO_IMAGE_OPTIMIZER_PORT is not valid number')
      process.exit(1)
    }

    // let the router know where to proxy image optimizer requests
    router.setBackend(BACKENDS.imageOptimizer, {
      domainOrIp: process.env.EDGIO_IMAGE_OPTIMIZER_HOST,
      port: imageOptimizerPort ? parseInt(imageOptimizerPort) : undefined,
    })
  }
}
/**
 * Starts the user's SSR app if it hasn't already been started
 */
let appStartedPromise: Promise<void>
async function ensureAppStarted(port: number) {
  if (!appStartedPromise) {
    appStartedPromise = startApp(port)
  }
  return appStartedPromise
}

// TODO: We need to implement a way to clearly split logs between:
//       1) user code logs that needs to be JSON, flagged with { awsTag: 'userLogs' }
//         and sent to STDOUT to be picked up by Cloudwatch and our Log Streamer
//       2) our internal wrapper exception logs that needs to be forwarded and handled
//         by XBP
//
//  1) is correctly implemented by stdStreamsWrapper, *but* we need to make sure that user code errors
//     cannot be logged to stdoud past `stdStreamsWrapper.disable()` otherwise they would not appear in Log Streamer
//
//  2) https://moovweb.atlassian.net/browse/PC-1766, exception logging and shipping in XBP needs
//     to be reviewed:
//       - some exceptions are escaping (some unhandled promise rejection for example)
//       - XBP is logging all access logs, but should only log exception traces (which should trigger alert)
/**
 * Starts collecting stdout
 * @param req The web request, if we're serving one
 */
function startLogging(req?: any, lambdaInstance?: LambdaInstance) {
  if (process.env.EDGIO_LOCAL !== 'true' || process.env.EDGIO_LOCAL_CONSOLE_WRAP === 'true') {
    // don't enable logging when running in local production mode because it makes debug output harder to read
    stdStreamsWrapper.enable({
      clientIp: req && req.headers[HTTP_HEADERS.x0ClientIp],
      requestId: req && req.headers[HTTP_HEADERS.xRequestId],
      wi: lambdaInstance?.id,
    })
  }
}

/**
 * Stops collecting stdout
 */
function stopLogging() {
  stdStreamsWrapper.disable()
}

// this allows to call deploy action with streaming enabled. This is meant for internal functions such as getPreloadConfig
// that is called after a deployment being successful. We don't need to wrap the edgeConfig call since it will never be available for the user
// since it runs before the deployment has finished and user has no way to access those logs.
async function withLogging(action: Function, req?: any, lambdaInstance?: LambdaInstance) {
  try {
    startLogging(req, lambdaInstance)
    return await action()
  } finally {
    stopLogging()
  }
}
