import { gunzipSync, brotliDecompressSync } from 'zlib'
import { HTTP_HEADERS, BROTLI_ENCODING, GZIP_ENCODING } from '../constants'
import Response from '../router/Response'

/**
 * If needed decodes the body in place from the response content-encoding.
 * Removes the content-encoding header and updates the content-length as the
 * decoded body has no encoding and will have a different length.
 * @param response
 * @returns True if decoding happened or wasn't needed, false otherwise
 */
export default function decode(response: Response): Boolean {
  const contentEncoding = (<string>response.getHeader(HTTP_HEADERS.contentEncoding))?.toLowerCase()
  // When there is no encoding, then there is nothing to decode.
  if (!contentEncoding) {
    return true
  }

  switch (contentEncoding) {
    case GZIP_ENCODING: {
      if (!Buffer.isBuffer(response.body)) {
        return false
      }
      response.body = gunzipSync(response.body)
      response.removeHeader(HTTP_HEADERS.contentEncoding)
      response.setHeader(HTTP_HEADERS.contentLength, Buffer.byteLength(response.body))
      return true
    }
    case BROTLI_ENCODING: {
      if (!Buffer.isBuffer(response.body)) {
        return false
      }
      response.body = brotliDecompressSync(response.body)
      response.removeHeader(HTTP_HEADERS.contentEncoding)
      response.setHeader(HTTP_HEADERS.contentLength, Buffer.byteLength(response.body))
      return true
    }
    default: {
      // We cannot decode the body for unsupported encodings.
      return false
    }
  }
}
